<template>
  <div class="remarks">
      <div class="top paddi">
          <textarea maxlength="60" placeholder="有什么特别的要求呢？" v-model="content"></textarea>
          <p class="three_t_l">{{content.length}}/60</p>
      </div>
      <div class="bottom paddi">
          <button class="bluebtn" @click="$router.push('/shoppingBag/comfirm')">提交</button>
      </div>
  </div>
</template>

<script>
export default {
  name: "Remarks",
  data() {
    return {
        content:''
    };
  },
  methods: {},
  components: {}
};
</script>

<style scoped lang="less">
.remarks {
    background: #f2f2f2;
    font-size: 0.16rem;
    padding-top: 0.1rem;
    min-height: 100vh;
    .paddi {
        padding-left: 0.28rem;
        padding-right: 0.27rem;
    }
    .top {
        background-color: #fff;
        textarea {
            width: 3.2rem;
            height: 1.34rem;
            resize: none;
            padding-top: 0.15rem;
            border: none;
            border-bottom: 0.01rem solid #f2f2f2;
        }
        p {
            text-align: right;
            height: 0.37rem;
            line-height: 0.37rem;
            margin-bottom: 0.3rem;
        }
    }
    .bottom {

    }
}
</style>



